.App {
  height: 100vh;
  width: 100vw;
  background-color: #F5F5F5;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
  
.wraper{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.signup-box {
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 26px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 993px;
  height: 531px;
}

.div-name {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 41px;
  width: 100%;
}

.label-data {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 21.3235px;
  color: #636363;
  margin-bottom: 10px;
  margin-top: 10px;
}

.div-data-email {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-left: 72px;
}

.div-password {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 21px;
  width: 100%;
}

.input-signup {
  box-sizing:border-box;
  width: 439px;
  height: 62px;
  border-radius: 10px;
  border: 2px solid #D1D1D1;
  box-shadow: none;
  margin-bottom: 15px;
  padding-left: 21px;
  font-size: 21.3235px;
}

.input-signup.-data {
  width: 189px;
  height: 62px;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 21.3235px;
  color: #636363;
}

.input-signup.-email {
  margin-top: 20px;
}

.input-signup:hover {
  border: 2px solid #1877F2;
}

.error-text {
  color: #DA5757;
  font-weight: 400;
  font-size: 16px;
  margin-top: 0px;
}

.error-text.-inv {
  visibility: hidden;
}

.signup-button {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 21.3235px;
  line-height: 29px;
  color: #FFFFFF;
  width: 185px;
  height: 62px;
  background: #1877F2;
  border-radius: 10px;
  border: none;
  margin-top: 10px;
  margin-left: 730px;
}

.signup-button:hover {
  transform: scale(1.03);
  opacity: 0.9;
  cursor: pointer;
}  

.bar-element {
  width: 293px;
  height: 0px;
  border: 2px solid #D1D1D1;
  transform: rotate(-0.19deg);
  margin-bottom: 15px;
}
